import styles from './order-details.module.scss';
import { useEffect } from 'react';
import { useSelector, useDispatch } from '../../types/hooks';
import { CheckMarkIcon } from '@ya.praktikum/react-developer-burger-ui-components'
import Loader from '../loader/loader';
import React from 'react';
import { RESET_ORDER_NUMBER } from '../../services/actions/order-details';

export default function OrderDetails(): JSX.Element {
    const dispatch = useDispatch();
    const { orderNumber } = useSelector(store => store.orderDetails);

    useEffect(() => {
        return () => { dispatch({ type: RESET_ORDER_NUMBER }) }
    },[dispatch])

    return (
        !orderNumber
            ? (<Loader />)
            : (
                <div className={`${styles.card}`}>
                    <h3 className={`${styles.order} pt-3 text text_type_digits-large`}>{orderNumber}</h3>
                    <p className='text text_type_main-medium pt-1'>–ò–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä –∑–∞–∫–∞–∑–∞</p>
                    <span className={`${styles.icon} mt-20 mb-20`}><CheckMarkIcon type='primary'/></span>
                    <p className='text text_type_main-default'>–í–∞—à –∑–∞–∫–∞–∑ –Ω–∞—á–∞–ª–∏ –≥–æ—Ç–æ–≤–∏—Ç—å</p>
                    <p className='text text_type_main-default text_color_inactive pb-10 pt-2'>–î–æ–∂–¥–∏—Ç–µ—Å—å –≥–æ—Ç–æ–≤–Ω–æ—Å—Ç–∏ –Ω–∞ –æ—Ä–±–∏—Ç–∞–ª—å–Ω–æ–π —Å—Ç–∞–Ω—Ü–∏–∏</p>
                </div>
            )
    )
}