import { Button, Input, PasswordInput } from '@ya.praktikum/react-developer-burger-ui-components'
import styles from './reset-password.module.scss';
import { Redirect, useHistory } from 'react-router-dom';
import { useSelector, useDispatch } from '../../types/hooks';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { PASSWORD_FORGOT_RESTORE } from '../../services/actions/forgot-password';
import { resetPassword } from '../../services/actions/auth/password';

export default function ResetPassword(): JSX.Element {
    const history = useHistory();
    const dispatch = useDispatch();
    const isPasswordForgotten = useSelector(store => store.forgotPassword.isPasswordForgotten);

    const user = useSelector(store => store.user);

    const [form, setForm] = useState({ password: '', token: '' });

    const onFormChange = (e: React.FormEvent<HTMLInputElement>) => {
        const formValue = e.currentTarget.value;
        const formName = e.currentTarget.name;
        setForm(prev => ({...prev, [formName]: formValue }))
    };

    const onFormSubmit = (e: React.SyntheticEvent) => {
        e.preventDefault();
        dispatch({ type: PASSWORD_FORGOT_RESTORE });
        dispatch(resetPassword(form));
        history.replace({ pathname: '/login' });
    }

    if (!isPasswordForgotten) {
        return (
            <Redirect to={{ pathname: '/' }} />
        )
    }

    if (user.username) {
        return (
            <Redirect to={{ pathname: '/' }} />
        )
    }

    return (
        <div className={styles.main}>

            <p className="text text_type_main-large">
                –í–æ—Å—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–∞—Ä–æ–ª—è
            </p>

            <form onSubmit={onFormSubmit} className={styles.form}>
                <PasswordInput
                    onChange={onFormChange}
                    value={form.password}
                    name={'password'}
                />

                <div className={styles.formChild}>
                    <Input
                        type={'text'}
                        placeholder={'–í–≤–µ–¥–∏—Ç–µ –∫–æ–¥ –∏–∑ –ø–∏—Å—å–º–∞'}
                        onChange={onFormChange}
                        value={form.token}
                        name={'token'}
                        error={false}
                        errorText={'–û—à–∏–±–∫–∞'}
                        size={'default'}
                    />
                </div>

                <div className={styles.formChild}>
                    <Button type="primary" size="large">
                        –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
                    </Button>
                </div>

            </form>

            <p className="text text_type_main-default text_color_inactive">
                –í—Å–ø–æ–º–Ω–∏–ª–∏ –ø–∞—Ä–æ–ª—å? <Link to={{ pathname: '/login' }}>–í–æ–π—Ç–∏</Link>
            </p>

        </div>
    )
}